
.container {
width: 350px;
height: 550px;
margin: 20px auto;
background: rgb(18, 10, 6);
border-radius: 8px;
}

.newBadge{
    width: 50px;
    height: 20px;
    padding: 5px;
    margin: 2px;
    background-color: rgb(244, 116, 11);
    border-radius: 5px;
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    font-style: italic;

}

.container img {
width:100%;
height: 100%;
object-fit: contain;
}



.content {
    padding-left: 15px;
    padding-right: 15px;
    font-family: 'Times New Roman', Times, serif;
    color: rgb(223, 238, 85);
}

.Online{
    color: whitesmoke;
    padding: 5px;
    margin-top: 60px; 
    background-color: rgb(199, 199, 50);
}